<!-- 视频管理列表 -->
<template>
  <div class="list-common-box">
    <!-- 标题 -->
    <div class="title-box">
      <Breadcrumb :isLevel='2'></Breadcrumb>
      <div class="rightbox">
        <div class="item" @click="handleAdd()">
           <cc-svg-icon icon-class="upload"></cc-svg-icon>
           <span>上传视频</span>
        </div>
      </div>
    </div>
    <!-- 主要数据 -->
    <div class="card-box">
      <!-- 搜索 -->
      <div class="search-bar">
        <!-- 左 -->
        <div class="search-left">
          <div class="search-item">
            <span class="search-label">视频名称</span>
            <el-input v-model.trim="searchKey.videoName" placeholder="请输入视频名称" clearable></el-input>
          </div>
          <div class="search-item">
            <span class="search-label">状态</span>
            <el-select placeholder="请选择状态" v-model="searchKey.videoStatus" clearable>
              <el-option v-for="item in statusOption" :key="item.value" :label="item.label" :value="item.value">
              </el-option>
            </el-select>
          </div>
          <div class="search-item">
            <span class="search-label">上传时间</span>
            <el-date-picker v-model="date" type="daterange" align="left" range-separator="至" start-placeholder="开始日期" end-placeholder="结束日期" :picker-options="pickerOptions" @change="handleDatechange" value-format="yyyy-MM-dd" clearable></el-date-picker>
          </div>
        </div>
        <div class="search-right">
          <el-button type="primary" plain icon="el-icon-search" @click="querylist()">查询</el-button>
          <el-button type="primary" plain icon="el-icon-delete" @click="Refreshlist()">清空</el-button>
        </div>
      </div>
      <!-- 表格 -->
      <div class="table-box">
        <el-table :data="dataList" tooltip-effect="dark" stripe height="100%"> 
          <el-table-column label="序号" align="center" width="80">
            <template slot-scope="scope">
              {{scope.$index+(pageNum - 1) * pageSize + 1}}
            </template>
          </el-table-column>
          <el-table-column prop="videoName" label="视频名称" align="center">
            <template slot-scope="scope">
              {{scope.row.videoName?scope.row.videoName:EMPTYTEXT}}
            </template>
          </el-table-column>
          <el-table-column prop="fileFormat" label="格式" align="center">
            <template slot-scope="scope">
              {{scope.row.fileFormat?scope.row.fileFormat:EMPTYTEXT}}
            </template>
          </el-table-column>
          <el-table-column prop="fileName" label="文件名" align="center">
            <template slot-scope="scope">
              {{scope.row.fileName?scope.row.fileName:EMPTYTEXT}}
            </template>
          </el-table-column>
          <el-table-column prop="videoStatus" label="状态" align="center">
            <template slot-scope="scope">
              {{scope.row.videoStatus===1?'已启用':scope.row.videoStatus===2?'已禁用':EMPTYTEXT}}
            </template>
          </el-table-column>
          <el-table-column prop="registerUser" label="上传人员" align="center">
            <template slot-scope="scope">
              {{scope.row.registerUser?scope.row.registerUser:EMPTYTEXT}}
            </template>
          </el-table-column>
          <el-table-column prop="createDateStr" label="上传时间"  align="center">
            <template slot-scope="scope">                  
              {{scope.row.createDateStr?scope.row.createDateStr:EMPTYTEXT}}
            </template>
          </el-table-column>
          <el-table-column label="操作"  align="center" width="120">
            <template slot-scope="scope">
              <cc-svg-icon icon-class="edit" @click="handleAdd(scope.row.videoId)"></cc-svg-icon>
              <cc-svg-icon icon-class="delete" @click="handleDelete(scope.row)"></cc-svg-icon>
              <cc-svg-icon icon-class="play" @click="handlePlay(scope.row)"></cc-svg-icon>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <!-- 分页 -->
      <div class="page-box">
         <Paginaction class='pagination' :goPage="pageNum" :totalNum="totalNum" @updateList="changePage" ref="paginationEvent"/>
      </div>
    </div>
    <!-- 新增、编辑视频弹窗 -->
    <addVideo v-model="videoDialog" :id="videoId" @refresh="getDatalist"></addVideo>
    <!-- 播放视频 -->
    <el-dialog :title="videoInfo.videoName" :visible.sync="playVideoDialog" center class="video-play-box">
      <video :src="videoInfo.fileUrl" controls="controls">
         您的电脑暂不支持视频播放
      </video>
    </el-dialog>
  </div>
</template>

<script>
import dateConfig from "../../../util/dateConfig.js" //日期选择配置项
import addVideo from './add' // 新增、编辑视频弹窗
  export default {
    name: 'video-list',
    components: {
      addVideo
    },
    data() {
      return {
        date:'',//时间
        searchKey:{
          videoName:'',//视频名称
          videoStatus:'',//视频状态
          startDate:'', //开始时间
          endDate:'',//结束时间
        },
        pickerOptions:dateConfig, //日期选择便捷选项
        statusOption:[
          {
            value: 0,
            label: '全部'
          },
          {
            value: 1,
            label: '已启用'
          },
          {
            value: 2,
            label: '已禁用'
          }
        ],
        pageNum:1,//当前页
        pageSize:10,//每页条数
        totalNum: 0, //总条数
        dataList: [],//视频列表
        videoDialog:false, //视频弹窗
        videoId:'',//视频id
        playVideoDialog:false, //播放视频
        videoInfo:{},//视频信息
      }
    },
    created() {
      this.getDatalist() //获取视频列表
    },
    methods: {
      //添加、编辑视频
      handleAdd(id) {
        this.videoDialog = true
        this.videoId = id?id:''
      },
      //播放
      handlePlay(scope){
        this.playVideoDialog = true;
        this.videoInfo = scope?scope:{}
      },
      //日期发生改变
      handleDatechange(val) {
        if (val && val.length > 0) {
          this.searchKey.startDate = val[0];
          this.searchKey.endDate = val[1];
        }else{
          this.searchKey.startDate = ''
          this.searchKey.endDate = ''
        }
      },
      //查询
      querylist() {
        this.pageNum = 1
        this.getDatalist()
      },
      //重置
      Refreshlist() {
        this.pageNum = 1
        this.pageSize = 10
        this.date=''
        Object.keys(this.searchKey).forEach(k => this.searchKey[k] = '')
        this.getDatalist()
      },
      //分页改变
      changePage(query){
        this.pageNum = query.pageNum
        this.pageSize = query.pageSize
        this.getDatalist()
      },
      //获取视频列表
      getDatalist(query) {
        this.$emit("viewLoad", true);
        let params = {}
        params.pageNum  = this.pageNum
        params.pageSize = this.pageSize
        params = Object.assign({},params,this.searchKey)
        this.$http.post(process.env.VUE_APP_API + '/family/video/getByPage', params).then(res => {
          if (res.code===200) {
            this.dataList = res.data.list || [] //视频列表
            this.totalNum = res.data.total //总条数
            this.pageNum = res.data.pageNum //当前页
            this.pageSize = res.data.pageSize //每页条数
          } else {
            this.$message.error(res.msg);
          }
          this.$emit("viewLoad", false);
        })
        .catch(error => {
          this.$emit("viewLoad", false);
          this.$message.error(error);
        });
      },
      //删除视频
      handleDelete (scope) {
        this.$confirm('确定删除"'+scope.videoName+'"吗？', '提示', {
          confirmButtonText: '确 认',
          cancelButtonText: '取 消',
          customClass:'infoMessage',
          center:true
        }).then(() => {
          this.$http.post(process.env.VUE_APP_API+'/family/video/delete',{
            videoId:scope.videoId
          }).then(res => {
            if(res.code===200){
              this.$message.success('删除成功');
              this.getDatalist() //刷新列表
            }else{
              this.$message.error(res.msg);
            }
          })
          .catch(error => {
            this.$message.error(error);
          });
        })
      }
    }
  }
</script>
<style lang='less'>
.video-play-box{
  .el-dialog{
    margin-top:7vh !important;
  }
  .el-dialog--center .el-dialog__body{
    max-height:70vh;
  }
  video{
    width:100%;
  }
}
</style>