<template>
  <el-dialog :title="id?'修改视频':'上传视频'"  :visible.sync="show" center width="550px" @close="resetForm('ruleForm')" :close-on-click-modal="false">
    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" class="ruleForm" label-width="110px">
      <el-form-item label="视频名称：" prop="videoName">
        <el-input v-model.trim="ruleForm.videoName" placeholder="请输入视频名称" class="input-box" clearable maxlength="50"></el-input>
      </el-form-item>
      <el-form-item label="视频大小：" prop="fileSize">  
         <el-input v-model.trim="ruleForm.fileSize" placeholder="视频大小" class="input-box" clearable disabled></el-input>
      </el-form-item>
      <el-form-item label="存储路径：">  
         <el-input type="textarea" v-model.trim="ruleForm.fileUrl" placeholder="存储路径" class="input-box" clearable disabled autosize></el-input>
      </el-form-item>
      <el-form-item label="文件格式" prop="fileFormat">  
         <el-input v-model.trim="ruleForm.fileFormat" placeholder="文件格式" class="input-box" clearable disabled></el-input>
      </el-form-item>
       <el-form-item label="状态：" prop="videoStatus">
         <el-select placeholder="请选择状态" v-model="ruleForm.videoStatus" class="input-box">
          <el-option v-for="item in statusOption" :key="item.value" :label="item.label" :value="item.value">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="上传文件：" prop="fileUrl">  
        <FileUpload :fileType="['.mp4','.rmvb']" @change="fileChange" :files="fileList"></FileUpload>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="resetForm('ruleForm')">取 消</el-button>
      <el-button type="primary" @click="submitForm('ruleForm')">确 认</el-button>
    </div>
 </el-dialog>
</template>

<script>
import formRule from '../../../util/formRule' // 表单验证
export default {
  name: 'addSoftware',
  props: {
    value: {
      type: Boolean
    },
    id:{
      type:[String,Number],
      value:''
    }
  },
  model: {
    prop: 'value',
    event: 'change'
  },
  watch: {
    value (n) {
      this.show = this.value
      if(this.show === true){
        if(this.id){
          this.getVideoDetail() //获取视频详情
        }else{
          this.initForm() //初始化表单
        }
      }
    }
  },
  data () {
    return {
       show:this.value,
       ruleForm:{
         videoName:'', //视频名称
         fileName:'',//文件名称
         fileSize:'', //文件大小
         fileFormat:'', //文件格式
         fileUrl:'', //视频地址
         videoStatus:1 //视频状态
       },
       fileList:[],//文件集合
       statusOption:[
          {
            value: 1,
            label: '已启用'
          },
          {
            value: 2,
            label: '已禁用'
          }
       ],//状态下拉
       rules: {
        videoName: [
          { required: true, message: 'APP名称不能为空', trigger: 'blur' }
        ],
        fileUrl: [
          { required: true, message: '请上传视频', trigger: 'change' }
        ]
      }
    }
  },
  methods: {
    //获取视频详情
    getVideoDetail(){
      this.$http.get(process.env.VUE_APP_API+'/family/video/getByVideoId?videoId='+this.id).then(res => {
        if(res.code===200){
          let result = res.data
          for(let key in this.ruleForm){
            this.ruleForm[key] = result[key]
          }
          //默认视频列表
          this.fileList = []
          let obj = {}
          obj.name = result.fileName?result.fileName:result.fileUrl
          obj.url = result.fileUrl
          obj.fileSize = result.fileSize
          obj.fileFormat = result.fileFormat
          this.fileList.push(obj)
        }else{
          this.$message.error(res.msg);
        }
      })
      .catch(error => {
        this.$message.error(error);
      });
    },
    //上传文件发生改变
    fileChange(result){
      if(result&&result.length>0){
        this.fileUrl = result
        this.ruleForm.fileName = result[0].name
        this.ruleForm.fileUrl = result[0].url
        this.ruleForm.fileSize = result[0].fileSize
        this.ruleForm.fileFormat = result[0].fileFormat
      }else{
        this.ruleForm.fileName = ''
        this.ruleForm.fileUrl = ''
        this.ruleForm.fileSize = ''
        this.ruleForm.fileFormat = ''
      }
      this.$refs.ruleForm.validateField('fileUrl')
    },
    //保存设置
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          if(this.id){
            this.ruleForm.videoId = this.id
            this.editVideo(this.ruleForm)
          }else{
            delete this.ruleForm.videoId
            this.addVideo(this.ruleForm)
          }
        }
      });
    },
    //添加视频
    addVideo(params){
      this.$http.post(process.env.VUE_APP_API+'/family/video/insert',params).then(res => {
        if(res.code===200){
          this.$message.success('添加成功');
          this.show = false
          this.$emit('change', false)
          this.$emit('refresh') //刷新列表
        }else{
          this.$message.error(res.msg);
        }
      })
      .catch(error => {
        this.$message.error(error);
      });
    },
    //编辑视频
    editVideo(params){
      this.$http.post(process.env.VUE_APP_API+'/family/video/update',params).then(res => {
        if(res.code===200){
          this.$message.success('修改成功');
          this.show = false
          this.$emit('change', false)
          this.$emit('refresh') //刷新列表
        }else{
          this.$message.error(res.msg);
        }
      })
      .catch(error => {
        this.$message.error(error);
      });
    },
    //初始化表单
    initForm(){
      Object.keys(this.ruleForm).forEach(k => this.ruleForm[k] = '')
      this.ruleForm.videoStatus = 1
      this.fileList = []
    },
    //取消设置
    resetForm(formName) {
      this.show = false
      this.$emit('change', false)
      this.$nextTick(()=>{
        this.$refs[formName].clearValidate();
      })
    }
  }
}
</script>

<style lang='less' scoped>
  /* 修改状态弹窗 */
  .ruleForm{
    width:410px;
    margin:0px auto;
    .input-box{
      width:300px;
      height:auto;
    }
  }
</style>